.page {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  position: fixed;
  // text-align: center;
  background-color: $color-background;
  background-image: $image-background;
}
.container {
  top: 2%;
  height: 97%;
  width: 84%;
  max-width: 1050px;
  position: relative;
  overflow: none;
  margin: 0 auto;
  box-sizing: border-box; // interior border
  background-color: rgba($color-post-background, 0.72);
}
.div_article {
  top: $header-height + 1px;
  height: 95%;
  width: 100%;
  position: relative;
  z-index: 1;
  overflow-x: hidden;
  border: $border-secondary;
  border-top: none;

  @include thin_scrollbar;
  article {
    margin: 0;
    padding: 10px 19px 19px 19px;
    min-height: 88%; 
  .content {
    text-align: justify !important; /* 1. 实现两端对齐 */
    text-justify: inter-character;  /* 2. 优化中文排版：调整字符间距而非仅词间距 */
    word-break: break-all;          /* 3. 解决西文/长单词导致的巨大空隙 (避头尾法则) */
    text-align-last: left;          /* 4. 强制段落最后一行左对齐 (默认行为，显式声明以防万一) */
  }
  }
  table {
    color: $color-third;
    margin: 10px 0 13px 0;
  }
  th {
    border: $border-dotted-third; 
  }
  td {
    border: $border-dotted-third;  
  }
  .encrypted {
    color: rgba($color-warning, 0.9);
    word-break: break-all;
  }
  .no-border {
    td {
      border: 1px solid rgba($color-post-background, 0);
    }
  }
}
// used to toggle absence of elements
.show {
  visibility: visible !important;
  @include fade_in;
}

// screen auto-adjustment
@media screen and (max-width: 1000px) {
  .page .container {
    width: 100% !important; // to avoid the affect of applyment of toggle_maximize()
    height: 100% !important;
    top: 0 !important;
    left: 0 !important;
    margin: 0;
    border: $border-thick;
    background-color: rgba($color-post-background, 0.7);
  }
  .page .container .div_article {
    border: none;
  }
}
@media screen and (max-width: 800px) and (min-width: 400px) {
  .page .container .div_article #main_atcl {
    padding: 12px 17px 17px 17px;
  }  
}
@media screen and (max-width: 400px) {
  .page .container {
    border: $border-normal;
  }
  .page .container .div_article #main_atcl {
    padding: 0px 5px 5px 5px ;
  }
}
